import { Component, ViewChild } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { IonicPage, NavController, Events } from 'ionic-angular';
import { Session, StorageService, } from '../../../services/services';
import { AuthProvider, WidgetProvider } from '../../../providers/providers';
import { Webpage } from '../../../models/models';

@IonicPage()
@Component({
  selector: 'page-search',
  templateUrl: 'search.html'
})
export class SearchPage {

  @ViewChild("keyword") keywordInput;
  searchList: any[];
  historyList: any[];
  historyRecord: any[];
  isShowRecord: Boolean = true;
  inputValue: any = '';

  isShowInput: Boolean;
  pageIndex: number = 0;
  pageSum: number = 0;
  seartchText: any = '';
  pageNumber: number = 30;
  debounce: number = 2000;
  finiteScroll: Boolean = true;
  isLoading: Boolean = false;
  constructor(

    public navCtrl: NavController,
    public authProvider: AuthProvider,
    public widgetProvider: WidgetProvider,
    public session: Session,
    private translateService: TranslateService,
    public storageService: StorageService,
    public events: Events
  ) {
    this.init();


    this.events.subscribe('user:logined', (time) => {
      this.SearchInfo(this.inputValue.trim());
    });

  }
  //开启页面获取焦点
  ionViewDidLoad() {
    // setTimeout(() => {
    //   this.keywordInput._searchbarInput.nativeElement.focus();
    // }, 1000); 
  }

  //离开页面时透明input
  ionViewWillLeave() {
    this.isShowInput = false;
  }

  //进入页面时显示input
  ionViewWillEnter() {
    this.isShowInput = true;
  }

  //页面完成时input自动获取焦点
  ionViewDidEnter() {
    setTimeout(() => {
      this.keywordInput._searchbarInput.nativeElement.focus();;//为输入框设置焦点
    }, 150);
  }
  //输入数据实时调用
  getItems(ev) {
    // set val to the value of the searchbar
    const val = ev.target.value;
    // if the value is an empty string don't filter the items
    if (val && val.trim() != '') {
      this.finiteScroll = true;
      this.pageIndex = 0;
      this.searchList = [];
      this.SearchInfo(val.trim());
    } else {
      //清空值得时候显示搜索记录
      this.isShowRecord = true;
    }

  }
  //根据历史记录搜索
  getSearch(e) {
    this.finiteScroll = true;
    this.inputValue = e;
    this.pageIndex = 0;
    this.searchList = [];
    this.SearchInfo(this.inputValue);
  }
  //记录搜索的历史记录
  addHistory(historyNew) {
    //找到相同值的index
    let index = this.historyList.findIndex((p) => p == historyNew);
    if (index > -1) {
      //找到相同的值，先删除掉，再插入到第0位
      this.historyList.splice(index, 1);
    }
    //插入到第0位
    this.historyList.unshift(historyNew);
    //搜索记录只保留最后10条

    if (this.historyList.length > 10) {
      this.historyList.splice(10, 1);
    }
    this.historyRecord = this.historyList;


    this.storageService.setItem("HistorySearch", this.historyList);

  }
  //搜索集
  SearchInfo(keyword) {
    if (keyword) {
      this.addHistory(keyword);
      this.isShowRecord = false;
      this.seartchText = keyword;
      this.isLoading = true;
      this.widgetProvider.getSearch({
        "keyword": keyword,
        "skip": this.pageIndex,
        "take": this.pageNumber
      }).subscribe((data: any) => {
        this.isLoading = false;
        if (data.errcode == 0) {
          this.pageSum = data.result.totalCount;
          for (let info in data.result.items) {
            this.searchList.push(data.result.items[info]);
          }
          if (this.pageSum == this.searchList.length) {
            //搜索结果已经全部输出
            // let more = {
            //   "id": 0,
            //   "type": null,
            //   "title": "<div class='pageEnd'>" + this.translateService.instant('HOME_SEARCH_PERIOD') + "</div>",
            //   "description": null,
            //   "imageUrl": null,
            //   "url": null,
            //   "remark": null
            // }
            // this.searchList.push(more);
            this.finiteScroll = false;

          }
          // setTimeout(() => {
          //   this.keywordInput._searchbarInput.nativeElement.focus();
          // }, 1000);
        }

      }, err => {
        this.isLoading = false;
        console.error('ERROR', JSON.stringify(err));
      });
    }

  }
  //应用跳转
  openArticle(article: any) {
    if (article == undefined || article == null || article.id == 0) return;
    let page: Webpage = {
      url: article.url,
      title: article.type,
      description: article.description,
      imageUrl: article.imageUrl,
      options: { }
    }
    this.widgetProvider.openWebpage(page, this.navCtrl);
  }
  //上滑加载数据
  doInfinite(infiniteScroll) {

    this.pageIndex += this.pageNumber;
    this.SearchInfo(this.seartchText);
    setTimeout(() => {
      infiniteScroll.complete();
    }, 1000);
  }

  clearAllHistory(){
    this.historyRecord = [] 
    this.historyList = []
    this.storageService.setItem("HistorySearch", this.historyList);
  }

  async init(){
    const history = await this.storageService.getItem("HistorySearch"); 
    if (history) {
      this.historyList = history;
      this.historyRecord = this.historyList;
    }else {
      this.historyList = [];
    }
  }
}




